import React from "react";
import "../App.css";
import ReactApexChart from "react-apexcharts";

function RadialGraph() {
    const series = [76, 67, 61, 90];
            const options = {
              chart: {
                height: 390,
                type: 'radialBar',
              },
              plotOptions: {
                radialBar: {
                  offsetY: 0,
                  startAngle: 0,
                  endAngle: 270,
                  hollow: {
                    margin: 5,
                    size: '30%',
                    background: 'transparent',
                    image: undefined,
                  },
                  dataLabels: {
                    name: {
                      show: false,
                    },
                    value: {
                      show: false,
                    }
                  }
                }
              },
              colors: ['#1ab7ea', '#0084ff', '#39539E', '#0077B5'],
              labels: ['Cooler', 'Washing Machine', 'Refrigerator', 'AC'],
              legend: {
                show: true,
                floating: true,
                fontSize: '16px',
                position: 'left',
                offsetX: 160,
                offsetY: 15,
                labels: {
                  useSeriesColors: true,
                },
                markers: {
                  size: 0
                },
                formatter: function(seriesName, opts) {
                  return seriesName + ":  " + opts.w.globals.series[opts.seriesIndex]
                },
                itemMargin: {
                  vertical: 3
                }
              },
              responsive: [{
                breakpoint: 480,
                options: {
                  legend: {
                      show: false
                  }
                }
              }]
            };
    return(
        <div style={{
            backgroundColor:"white",
            textAlign:"center",
        }}>
            <br/>
            <h2>Monthly Energy Usage Per Appliance</h2>
            <br/>
            <ReactApexChart options={options} series={series} type="radialBar" height={390} />
            <br/>
        </div>
    );
}

export default RadialGraph;